
























































import Home from "./components/home/home"
import Sprites from "./components/gallery/sprites"
import Editor from "./components/editor/editor"

export default

	components:
		"home": Home
		"sprites": Sprites
		"editor": Editor

	data: () ->
		sideBarDisplay: "block"
		currentTab: "sprites"
		currentSprite: null

	methods:
		selectTab: (tab) ->
			this.currentTab = tab

		editSprite: (sprite) ->
			this.currentSprite = sprite
			this.currentTab = "editor"

